 ease;\n    border-radius: 10px;\n    border: none;\n    padding: 17px 0;\n    margin-top: 8px;\n    margin-bottom: 16px;\n    font-weight: $semi-bold;\n    font-size: 20px;\n\n    &.btn-primary {\n        background-color: $violett;\n        color: $white;\n\n        &:hover {\n            //background-color: darken($blue-light, 5%);\n        }\n\n        &.loading {\n            position: relative;\n            height: 52px;\n\n            .loader-wrapper {\n                position: absolute;\n                top: calc(50% - 12px);\n                left: 50%;\n                transform: translate(-50%, -50%);\n\n                // see .loader\n            }\n        }\n    }\n\n    &.btn-secondary {\n        background-color: $white;\n        color: $black;\n        border: 1px solid $black;\n    }\n}\n\n.question {\n    h1 {\n        font-size: 38px;\n        line-height: 1.2;\n        font-weight: $regular;\n        text-align: center;\n        margin-bottom: 25px;\n\n        span {\n            font-weight: $semi-bold;\n        }\n    }\n}\n\n.or-separator {\n    p {\n        overflow: hidden;\n        text-align:center;\n        font-size: 14px;\n        line-height: 14px;\n        color: $black;\n        margin-bottom: 10px;\n\n        &:before {\n            right: 10px;\n            margin-left: -50%;\n        }\n\n        &:after {\n            left: 10px;\n            margin-right: -50%;\n        }\n\n        &:before, \n        &:after {\n            background-color: $black;\n            content: \"\";\n            display: inline-block;\n            height: 1px;\n            position: relative;\n            vertical-align: middle;\n            width: 50%;\n            margin-top: -2px;\n        }\n    }\n}\n\n.story-card {\n    border-radius: 10px;\n    overflow: hidden;\n    margin-bottom: 10px;\n\n    .wrapper {\n        // background image via js\n        width: 100%;\n        height: 220px;\n        position: relative;\n\n        .overlay {\n            position: absolute;\n            width: 100%;\n            left: 0;\n            top: 0;\n            height: 220px;\n            background-color: rgba(0, 0, 0, .4);\n            z-index: 1;\n        }\n\n        .headlines {\n            position: absolute;\n            width: 100%;\n            left: 0;\n            bottom: 0;\n            z-index: 2;\n            padding: 15px 10px;\n\n            .subheadline {\n                color: $white;\n                margin-bottom: 10px;\n            }\n\n            .headline {\n                font-size: 25px;\n                color: $white;\n                font-weight: $semi-bold;\n                \n            }\n        }\n    }\n}\n\n\n\n.fade-appear,\n.fade-enter {\n    opacity: 0;\n}\n\n.fade-appear-active,\n.fade-enter-active {\n    transition: opacity 0.4s linear;\n    opacity: 1;\n}\n\n.fade-exit {\n    opacity: 0;\n}\n\n.fade-exit-active {\n    opacity: 0;\n}\n\n.slider-box {\n    width: 100%;\n    position: relative;\n\n    .label {\n        width: 30%;\n        position: absolute;\n        right: 0;\n\n        #non-linear-slider {\n            line-height: 46px;\n            vertical-align: middle;\n\n            font-family: 'Open Sans', sans-serif;\n            font-weight: $semi-bold;\n            font-size: 20px;\n            margin-left: 20px;\n\n        } \n    }\n\n     .MuiSlider-colorPrimary {\n         width: 65%;\n         left: 15px;\n     }\n}\n\n.sticky-bottom {\n    position: absolute;\n    bottom: 100px;\n    width: 90%;\n}","@-webkit-keyframes shake {\n\n  8%,\n  41% {\n    -webkit-transform: translateX(-10px);\n  }\n\n  25%,\n  58% {\n    -webkit-transform: translateX(10px);\n  }\n\n  75% {\n    -webkit-transform: translateX(-5px);\n  }\n\n  92% {\n    -webkit-transform: translateX(5px);\n  }\n\n  0%,\n  100% {\n    -webkit-transform: translateX(0);\n  }\n}\n\n@keyframes fadeInUp {\n  from {\n    transform: translate3d(0, 70px, 0)\n  }\n\n  to {\n    transform: translate3d(0, 0, 