-webkit-outer-spin-button {\n  -webkit-appearance: none;\n  margin: 0;\n}\n\ninput[type='number'] {\n  -moz-appearance:textfield;\n}","// colors\n$grey: #717171;\n$white: #FFFFFF;\n$black: #222222;\n$black-light: rgba($black, 0.7);\n$grey-light: lighten($black, 40%);\n\n$violett: #B6A6F8;\n\n$yellow: #EEBD41;\n$red: #D04F3A;\n$blue: #5887F1;\n$green: #63A557;\n$blue-light: #4697CE;\n\n// font weights\n$semi-bold: 600;\n$medium: 500;\n$regular: 400;\n$light: 300;\n\n// \n$content-margin: 30px;\n","\n.modal-is-open {\n    position: fixed;\n    overflow-y: scroll;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    z-index: 1;\n}\n\n.noscroll {\n    overflow: hidden;\n}\n\n.modal {\n    background-color: $white;\n\n    .modal-inner {\n        overflow: hidden;\n        min-height: 100vh;\n\n        .content {\n            margin: 2.618em auto 2em auto;\n\n            button {\n                margin-top: 5px;\n            }\n        }\n    }\n}",".fadeInOut-enter {\n    animation: fadein 150ms ease-in forwards; // has to be the same amount as in FadeInOut.jsx\n}\n\n.fadeInOut-exit {\n    animation: fadeout 150ms ease-in forwards; // has to be the same amount as in FadeInOut.jsx\n}\n\n@-webkit-keyframes fadein {\n    0% {\n        opacity: 0;\n    }\n\n    100% {\n        opacity: 1;\n    }\n}\n\n@keyframes fadein {\n    0% {\n        opacity: 0;\n    }\n\n    100% {\n        opacity: 1;\n    }\n}\n\n@-webkit-keyframes fadeout {\n    0% {\n        opacity: 1;\n    }\n\n    100% {\n        opacity: 0;\n    }\n}\n\n@keyframes fadeout {\n    0% {\n        opacity: 1;\n    }\n\n    100% {\n        opacity: 0;\n    }\n}\n\n\n\n"],"sourceRoot":""}]);
